import { Link } from "react-scroll";
import logo from "../../component/images/logos-removebg-preview.png";
import "./Navbar.css";
function Navbar(props) {
  
  return (
    <>
      <nav  id="home" className={`navbar mx-2 navbar-expand-lg navbar-${props.mode} bg-transparent`} >
        <div className="container-fluid">
          <Link  className="navbar-brand" to="/">
            <img style={{ width: "3rem" }} src={logo} alt="loading" />
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div
            className="collapse navattack  navbar-collapse h5
                  "
            id="navbarSupportedContent"
          >
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <li className="nav-item px-5">
                <span
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  title="Home"
                >
                  <Link to='app'
             spy={true}
              smooth={true}
              offset={50}
              duration={500}
                    className="nav-link  "
                    activeclassname="menu_active"
                    href="/"
                  >
                    Home
                  </Link>
                </span>
              </li>
              <li className="nav-item px-5">
                <span
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  title="Project"
                >
                  {" "}
                  <Link to='project'
             spy={true}
              smooth={true}
              offset={50}
              duration={500}
                    className="nav-link "
                    activeclassname="menu_active"
                    href="/project"
                  >
                    Project
                  </Link>
                </span>
              </li>
              <li className="nav-item px-5">
                <span
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  title="About"
                >
                  {" "}
                  <Link to='about'
             spy={true}
              smooth={true}
              offset={50}
              duration={500}
                    className="nav-link "
                    activeclassname="menu_active"
                    href="/about"
                  >
                    About
                  </Link>
                </span>
              </li>
              <li className="nav-item px-5 ">
                <span
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  title="Contact"
                >
                  {" "}
                  <Link to='contact'
             spy={true}
              smooth={true}
              offset={50}
              duration={500}
                    className="nav-link "
                    activeclassname="menu_active"
                    href="/contact"
                  >
                    Contact
                  </Link>
                </span>
              </li>
              <li className={`form-check mt-2 form-switch text-${props.mode==='light'?"dark":"light"}`}>
                <input onClick={props.togglemode} style={{cursor:"pointer"}}
                  className=" form-check-input"
                  type="checkbox"
                  role="switch"
                  id="dark"
                />
                <label className=" form-check-label" style={{textTransform:"capitalize"}} htmlFor="dark">
                {props.mode==='light'?"dark":"light"} Mode
                </label>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}

export default Navbar;
